.about-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  padding: 60px 80px;
}

/* LEFT TEXT SECTION */
.about-left {
  flex: 1.2;
}

.about-left p {
  font-size: 20px;
  line-height: 1.8;
  color: #333;
  max-width: 600px;
  text-align: left;
}

/* RIGHT IMAGE SECTION */
.about-right {
  flex: 1;
  display: flex;
  justify-content: center;
}

.about-right img {
  width: 350px;
  height: 350px;
  object-fit: cover;
  border-radius: 20px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.2);
}

/* MOBILE RESPONSIVE */
@media (max-width: 900px) {
  .about-container {
    flex-direction: column;
    text-align: center;
    padding: 40px 20px;
  }

  .about-left p {
    text-align: center;
  }

  .about-right img {
    width: 260px;
    height: 260px;
  }
}
